import React from "react";
import { Avatar } from "@material-ui/core";

import { useSelector } from "react-redux";
import { selectUser } from "../../features/userSlice";

import "./index.css";

const BACKGROUND_IMG =
  "https://st.depositphotos.com/1522993/4737/v/600/depositphotos_47372005-stock-illustration-orange-blue-background-with-triagles.jpg";
const Sidebar = () => {
  const user = useSelector(selectUser);

  const recentItems = (topic) => (
    <div className="sidebar__topic">
      <span className="sidebar__hash">#</span>
      <p>{topic}</p>
    </div>
  );
  return (
    <div className="sidebar">
      <div className="sidebar__top">
        <img src={BACKGROUND_IMG} alt="" />
        <Avatar className="sidebar__avatar" src={user.photoUrl}>
          {user.email[0]}
        </Avatar>
        <h2>{user.displayName}</h2>
        <h4>{user.email}</h4>
      </div>
      <div className="sidebar__stats">
        <div className="sidebar__stat">
          <p>¿Quién te visitó?</p>
          <p className="sidebar__stat-number">2,543</p>
        </div>
        <div className="sidebar__stat">
          <p>Vistas de publicaciones</p>
          <p className="sidebar__stat-number">2,843</p>
        </div>
      </div>
      <div className="sidebar__bottom">
        <p>Recientes</p>
        {recentItems("react-js")}
        {recentItems("programming")}
        {recentItems("desing")}
        {recentItems("development")}
      </div>
    </div>
  );
};

export default Sidebar;
