import React from "react";
import SearchIcon from "@material-ui/icons/Search";
import HeaderOption from "./HeaderOption";

import "./index.css";
import { linkedInIcon } from "../../assets";

import HomeIcon from "@material-ui/icons/Home";
import SupervisorAccountIcon from "@material-ui/icons/SupervisorAccount";
import BusinessCenterIcon from "@material-ui/icons/BusinessCenter";
import ChatIcon from "@material-ui/icons/Chat";
import NotificationsIcon from "@material-ui/icons/Notifications";
import { logout } from "../../features/userSlice";
import { useDispatch } from "react-redux";
import { auth } from "../../utils/firebase";
import { useSelector } from "react-redux";
import { selectUser } from "../../features/userSlice";


const Header = () => {
  const user = useSelector(selectUser);
  const dispatch = useDispatch();
  const logOutApp = () => {
    dispatch(logout());
    auth.signOut();
  };
  return (
    <div className="header">
      <div className="header__left">
        <img src={linkedInIcon} alt="LinkedIn Icon" />
        <div className="header__search">
          <SearchIcon />
          <input type="text" placeholder="Buscar..." />
        </div>
      </div>
      <div className="header__right">
        <HeaderOption title="Inicio" Icon={HomeIcon} />
        <HeaderOption title="Mi red" Icon={SupervisorAccountIcon} />
        <HeaderOption title="Empleos" Icon={BusinessCenterIcon} />
        <HeaderOption title="Mensajes" Icon={ChatIcon} />
        <HeaderOption title="Notificaciones" Icon={NotificationsIcon} />
        <HeaderOption
          title={user?.displayName}
          avatar={user?.photoUrl || user?.email[0]}
          onClick={logOutApp}
        />
      </div>
    </div>
  );
};

export default Header;
